import { actionTypes } from "../types";
import Axios from "axios";
export const mudaNome = texto => {
  return {
    type: actionTypes.MUDA_NOME,
    payload: texto
  };
};

export const mudaEmail = texto => {
  return {
    type: actionTypes.MUDA_EMAIL,
    payload: texto
  };
};

export const mudaSenha = texto => {
  return {
    type: actionTypes.MUDA_SENHA,
    payload: texto
  };
};

export const cadastrar = data => {
  return dispatch => {
    Axios.post(`${actionTypes.URL}signin`, data).then(res => {
      console.log(res);
      dispatch({ type: actionTypes.CADASTRADO_SUCESSO, payload: true})
    });
  };
};
